import React, {Component, Fragment} from 'react';
import './DialogBox.css';

class DialogBox extends Component {
    state = {
        totalZakat : ''
    }

    CloseDialogbox = () => {
        this.props.toCloseDialogbox(this.state.totalZakat)
    }


    render(){
        return(
            <Fragment>
                <div  className="overlay"></div>
                <div className="dialogContainer">
                    <div className="dialog">
                        <h2>Total Zakat</h2>
                        <p>{this.props.body}</p>
                        <div className="closeButtonWrap">
                            <button className="closeButton" type="button" onClick={this.CloseDialogbox}>Close</button>
                        </div>
                    </div>
                </div>
            </Fragment>
        )
    }
}

DialogBox.defaultProps = {
    body : "Perhitungan ini tidak memiliki hasil"
}

export default DialogBox;